import React, { Component } from 'react';
import './WorkoutInputs.css';

import { jsonContext } from '../Containers/Body'

function Intensity(){
  return Object.freeze({
    "low":{workTime: 45, pauseTime: 25, repeat: 2, sets: 4, name:"Low (45s/25s)", type:'repeat'},
    "high":{workTime: 30, pauseTime: 10, repeat: 3, sets: 3, name:"High (30s/10s)", type:'repeat'},
    "strength":{workTime: 90, pauseTime: 60, repeat: 3, sets: 5, name:"Strength (90s/60s)", type:'continuous'},
    "endurance":{workTime: 60, pauseTime: 45, repeat: 3, sets: 5, name:"Endurance (60s/45s)", type:'continuous'}
  });
}

function IntensityForInputs(filter=[]){
  var iteratables = [];
  for (const key in Intensity()) {
    if(filter.length !== 0 && !filter.includes(key))
      continue;

    var name = Intensity()[key].name;
    iteratables.push([
      name, key
    ]);
  }
  return iteratables;
}

function IntensitySelector(rootPath){
  if(rootPath === 'dumbell_exercises'){
    return IntensityForInputs(['strength', 'endurance']);
  }else if(rootPath === 'suspension_exercises'){
    return IntensityForInputs(['low', 'high']);
  }
}

class WorkoutInputs extends Component {
  render() {
    return(
      <jsonContext.Consumer>
        {json=>{
          return(
            <div className="WorkoutInputs">
              <div className="row show-inputs">
                <div className="col-sm-12 col-md-4">
                  {this.getList('Exercises', json.inputs.Exercises, 'checkbox')}
                </div>
                <div className="col-sm-12 col-md-4">
                  {this.getList('Levels', json.inputs.Levels, 'checkbox')}
                </div>
                <div className="col-sm-12 col-md-4">
                  {this.getList('Intensity', IntensitySelector(json.rootPath), 'radio')}
                </div>
              </div>
              <div className="row">
                <button 
                  type="button" 
                  className="btn btn-primary btn-lg btn-block" 
                  onClick={this.props.createButtonFn}
                >Create Random Exercise</button>
              </div>
            </div>
          );
        }}
      </jsonContext.Consumer>
    );
  }

  getList(name, object, type){
    var liArr = []
    for (const lis of object) {
      var par = {
        type: type,
        group: name,
        value: lis.slice(1).join(',')
      };
      liArr.push(
        <li className="list-group-item" key={lis[0]}>{lis[0]}{this.getInput(par)}</li>
      );
    }
    return(
      <ul className="list-group">
        <li className="list-group-item disabled">{name}</li>
        {liArr}
      </ul>
    );
  }

  getInput(par){
    return(
      <label className="switch">
        <input 
          type={par.type} 
          name={par.group} 
          value={par.value} 
          className="primary"
          onChange={this.props.checkBoxFn}
        />
        <span className="slider round"></span>
      </label>
    );
  }
}

export default WorkoutInputs;



