import React, { Component } from 'react';
import ExerciseFrame from './ExerciseFrame';
import WorkoutInputs from './WorkoutInputs';
import { Redirect } from "react-router-dom";

import $ from 'jquery';
import 'bootstrap';

import { jsonContext } from '../Containers/Body'

class ExerciseLister extends Component {
    constructor(props){
        super(props);

        Window.timerData = null;
        this._checkBoxFn = this._checkBoxFn.bind(this);
        this._createButtonFn = this._createButtonFn.bind(this);

        this.state = {
            'Exercises': [],
            'Levels': [],
            'Intensity': null,
            'reRender': false
        };
    }

    render() {
        return (!this.state.reRender)?
                (<div>
                    <WorkoutInputs
                        checkBoxFn={this._checkBoxFn}
                        createButtonFn={this._createButtonFn}
                    />
                    {this._getModal()}
                    <div className="text-center">
                        <jsonContext.Consumer>
                            {json=>{
                                return this._listing(json);
                            }}
                        </jsonContext.Consumer>
                    </div>
                </div>):
            (<Redirect to={process.env.PUBLIC_URL+"/timer"} />);
    }

    _getModal(){
        return(
            <div className="modal fade" id="exampleModal" tabIndex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title">Warning!</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            Please select Intensity.
                        </div>
                    </div>
                </div>
            </div>
        );
    }

    _listing(json){
        var filterE = 0 !== this.state['Exercises'].length
        var filterL = 0 !== this.state['Levels'].length;
        var frames = [];
        for (let x = 0; x < json.images.wlen; x++) {
            if(filterE && !this._include('Exercises', x.toString()))
            continue;

            frames.push(this._getColName(json, x));

            for (let y = 0; y < json.images.hlen; y++) {
                if(filterL && !this._include('Levels', y.toString()))
                    continue;

                var filename = json.images.exercises[y][x];
                if(filename){
                    frames.push(
                        <ExerciseFrame
                            rootPath = {json.rootPath}
                            filename = {filename}
                            show={true}
                            key={filename}
                        />
                    );
                }
            }      
        }
        return frames;
    }

    _getColName(json, actCol){
        for (const iterator of json.inputs.Exercises) {
            if(iterator[1] === actCol){
                return (<div className="display-4 text-left show-frame" key={iterator[0]}>{iterator[0]}</div>);
            }
        }
        return null;
    }

    _add(key, valueStr){
        this._iterateValues(key, valueStr, (oV, v, i)=>{
            if(!i){
                oV.push(v);
            }
            return oV;
        });
    }

    _del(key, valueStr){
        this._iterateValues(key, valueStr, (oV, v, i)=>{
            if(i){
                var index = oV.indexOf(v);
                if (index !== -1) {
                    oV.splice(index, 1);
                }
            }
            return oV;
        });
    }

    _iterateValues(key, valueStr, fn){
        var values = valueStr.split(',');
        var originalValues = this.state[key].slice();
        for (const value of values) {
            originalValues = fn(
                originalValues, 
                value, 
                this._include(key, value)
            );
        }
        var newState = {};
        newState[key] = originalValues.sort();
        this.setState(newState);
    }

    _include(key, value){
        return this.state[key].includes(value);
    }

    _checkBoxFn(e){
        if(e.target.name in this.state){
            if(e.target.name === 'Intensity'){
                this.setState({'Intensity': e.target.value});
            }else{
                if(e.target.checked){
                    this._add(e.target.name, e.target.value);
                }
                else{
                    this._del(e.target.name, e.target.value);
                }
            }
        }
    }

    _createButtonFn(){
        if(!this.state.Intensity){
            $('#exampleModal').modal();
            return;
        }
        
        Window.timerData = JSON.parse(JSON.stringify(this.state));
        this.setState({reRender: true});
    }
}

export default ExerciseLister;



