import React, { Component } from 'react';
import ExerciseLister from '../Components/ExerciseLister';

class Dumbell extends Component {
  constructor(props){
    super(props);
    
    this.state = {
      'rootPath': 'dumbell_exercises',
      jsonFile: null,
    };
  }

  componentDidMount(){
    fetch('/'+this.state.rootPath+'/'+this.state.rootPath+'.json')
    .then(response => {
      return response.json();
    })
    .then(myJson => {
      this.setState((prevState)=>{
        return {'jsonFile': myJson}
      });
    });
  }

  render() {
    return (this.state.jsonFile)?(
        <ExerciseLister 
          jsonFile={this.state.jsonFile} 
          rootPath={this.state.rootPath}
        />
    ):( <div></div> );
  }
}
Dumbell.rootPath = 'dumbell_exercises';

export default Dumbell;



