import React, { Component } from 'react';
import { Route } from "react-router-dom";

import Home from '../Pages/Home';
import Suspension from '../Pages/Suspension';
import Dumbell from '../Pages/Dumbell';
import Timer from '../Pages/Timer'

import Loader from '../Components/Loader'
import request from '../Components/request'

const jsonContext = React.createContext({});

class Body extends Component {
  constructor(props){
    super(props);

    this.rootPaths = [
      Suspension.rootPath,
      Dumbell.rootPath
    ];

    this.state = {};
  }

  componentWillMount(){
    for (const rootPath of this.rootPaths) {
        this._collectingData(rootPath);
    }
  }

  _collectingData(rootPath){
    var rootPathData = {
      rootPath: rootPath,
      images: null,
      inputs: null
    };
    
    this._saveState(rootPath, rootPathData);
    
    // images
    request(
      process.env.PUBLIC_URL+'/'+rootPath+'/'+rootPath+'.json',
      (json)=>{
        this.setState(state=>{
          return state[rootPath]['images'] = json
        });
      }
    );

    // inputs
    request(
      process.env.PUBLIC_URL+'/'+rootPath+'/inputs.json',
      (json)=>{
        this.setState(state=>{
          return state[rootPath]['inputs'] = json
        });
      }
    );
  }

  _saveState(rootPath, rootPathData){
    var tempState = {};
    tempState[rootPath] = rootPathData;
    this.setState(tempState);
  }

  _isNullState(rootPath){
    return this.state[rootPath].images === null || this.state[rootPath].inputs === null;
  }

  _renderExercises(RenderClass){
    if(!this._isNullState(RenderClass.rootPath)){
      return(
        <jsonContext.Provider value={this.state[RenderClass.rootPath]}>
          <RenderClass/>
        </jsonContext.Provider>
      );
    }else{
      return(
        <Loader/>
      );
    }
  }

  render() {
    return (
      <div className="container">
        <Route exact path={process.env.PUBLIC_URL+"/"} render={()=>{ 
          return <Home/>
        }} />

        <Route path={process.env.PUBLIC_URL+"/suspension"} render={()=>{
          return this._renderExercises(Suspension); 
        }} />

        <Route path={process.env.PUBLIC_URL+"/dumbell"} render={()=>{
          return this._renderExercises(Dumbell); 
        }} />

        <Route path={process.env.PUBLIC_URL+"/timer"} render={()=>{
          return <Timer/>;
        }} />
      </div>
    );
  }
}

export {Body, jsonContext};


