import React, { Component } from 'react';
import './ExerciseFrame.css';

class ExerciseFrame extends Component {
  render() {
    return(
       <div className={!this.props.show?"ex-frame":"ex-frame show-frame"}>
           <img 
            src={process.env.PUBLIC_URL+'/'+this.props.rootPath + '/' + this.props.filename} 
            alt={this.props.filename} 
            width="160"
           />
       </div>
    );
  }
}

export default ExerciseFrame;



